import { Component, OnInit } from '@angular/core';
import { TipoColeccion } from '../tipo-coleccion';
import { TipoTecnica } from '../tipo-tecnica';
import { ActivatedRoute, ParamMap, Route, Router } from '@angular/router';
import { ObraService } from '../obras.service';
import { TipoColeccionService } from '../obras-new/tipo-coleccion.service';
import { Obra } from '../obra';


@Component({
  selector: 'app-obras-edit',
  templateUrl: './obras-edit.component.html',
  styleUrls: ['./obras-edit.component.css']
})
export class ObrasEditComponent implements OnInit {

  obra: Obra;
  idObra: String;
  fechaInicio : Date
  fechaFin : Date
  tiposColecciones: TipoColeccion[]
  tiposTecnicas: TipoTecnica[]
  tipoColeccion: TipoColeccion
  tipoTecnica: TipoTecnica
  imageFile: File;

  constructor(private route: ActivatedRoute, 
    private obraService: ObraService,
    private tipoService: TipoColeccionService,
    private router: Router) {
    this.idObra = this.route.snapshot.paramMap.get('id')
    this.obraService.getObra(this.idObra)
      .subscribe(obra => {
        this.obra = obra[0]
        this.fechaFin = this.obra.FECHA_FIN
        this.fechaInicio = this.obra.FECHA_INICIO
      });
  }

  ngOnInit() {
    this.cargarTipos()
  }

  cargarTipos() {
    this.tipoService.getTiposColeccion().then(tipos => this.tiposColecciones = tipos)
    this.tipoService.getTiposTecnicas().then(tipos => this.tiposTecnicas = tipos)
  }

  cambioTipoColeccion(nuevoTipo) {
    this.tipoColeccion = nuevoTipo
  }

  cambioTipoTecnica(nuevoTipo) {
    this.tipoTecnica = nuevoTipo
  }

  changeImage(event) {
    console.log(this.obra)
    this.imageFile = <File>event.target.files[0]
  }

  editar(e) {
    e.preventDefault();
    this.obra.NOMBRE_ARTISTA = e.target.elements[0].value;
    this.obra.NOMBRE_DE_OBRA = e.target.elements[1].value;
    this.obra.LARGO = e.target.elements[2].value;
    this.obra.ANCHO = e.target.elements[3].value;
    this.obra.YEAR = e.target.elements[4].value;
    this.obra.FECHA_INICIO = e.target.elements[5].value;
    this.obra.FECHA_FIN = e.target.elements[6].value;
    this.obra.ID_TIPO_COLECCION = this.tipoColeccion.ID_TIPO_COLECCION
    this.obra.ID_TIPO_DE_TECNICA = this.tipoTecnica.ID_TIPO_DE_TECNICA
    this.obraService.uploadImage(this.imageFile).subscribe( result => {
      console.log(result)
    })
    this.obra.IMG_SOURCE = "images/" + this.imageFile.name
    this.obraService.edit(this.obra)
    this.router.navigate(['obras'])
  }
}
