import { Component, OnInit } from '@angular/core';
import { TipoColeccionService } from './tipo-coleccion.service';
import { TipoColeccion } from '../tipo-coleccion';
import { TipoTecnica } from '../tipo-tecnica';
import { Obra } from '../obra';
import { Router } from '@angular/router';
import { ObraService } from '../obras.service';

@Component({
  selector: 'app-obras-new',
  templateUrl: './obras-new.component.html',
  styleUrls: ['./obras-new.component.css']
})
export class ObrasNewComponent implements OnInit {

  tiposColecciones: TipoColeccion[]
  tiposTecnicas: TipoTecnica[]
  tipoColeccion: TipoColeccion
  tipoTecnica: TipoTecnica
  imageFile: File

  constructor(private tipoColeccionService: TipoColeccionService,
    private obraService: ObraService,
    private router:Router) { }

  ngOnInit() {
    this.cargarTipos()
  }

  cargarTipos() {
    this.tipoColeccionService.getTiposColeccion().then(tipos => this.tiposColecciones = tipos)
    this.tipoColeccionService.getTiposTecnicas().then(tipos => this.tiposTecnicas = tipos)
  }

  cambioTipoColeccion(nuevoTipo){
    this.tipoColeccion = nuevoTipo
  }

  cambioTipoTecnica(nuevoTipo){
    this.tipoTecnica = nuevoTipo
  }

  changeImage(event) {
    this.imageFile = <File>event.target.files[0]
    //this.obraService.uploadImage(<File>event.target.files[0]).subscribe( result => {
      //console.log(result)
    //})
  }

  agregar(e){
    e.preventDefault();
    var nombreArtista = e.target.elements[0].value;
    var nombreObra = e.target.elements[1].value;
    var largo = e.target.elements[2].value;
    var ancho = e.target.elements[3].value;
    var annio = e.target.elements[4].value;
    var fechaInicio = e.target.elements[5].value;
    var fechaFin = e.target.elements[6].value;
    var newObra = new Obra;
    newObra.NOMBRE_ARTISTA = nombreArtista
    newObra.NOMBRE_DE_OBRA = nombreObra
    newObra.YEAR = annio
    newObra.LARGO = largo
    newObra.ANCHO = ancho
    newObra.FECHA_FIN = fechaFin
    newObra.FECHA_INICIO = fechaInicio
    newObra.ID_TIPO_COLECCION = this.tipoColeccion.ID_TIPO_COLECCION
    newObra.ID_TIPO_DE_TECNICA = this.tipoTecnica.ID_TIPO_DE_TECNICA
    this.obraService.uploadImage(this.imageFile).subscribe( result => {
      console.log(result)
    })
    newObra.IMG_SOURCE = "images/" + this.imageFile.name
    this.obraService.create(newObra)
    this.router.navigate(['obras'])
  }
}
