export class Obra {
    constructor(
        public ID_OBRA_DE_ARTE: number = 0,
        public NOMBRE_DE_OBRA: string = "",
        public IMG_SOURCE: string = "",
        public NOMBRE_ARTISTA: string = "",
        public LARGO: number = 0,
        public ANCHO: number = 0,
        public YEAR: number = 0,
        public FECHA_INICIO: Date = null,
        public FECHA_FIN: Date = null,
        public ID_TIPO_COLECCION: number = 0,
        public ID_TIPO_DE_TECNICA: number = 0
    ) { }

    getFechaInicio() {
        var month = this.FECHA_INICIO.getMonth() + 1;
        var day = this.FECHA_INICIO.getDate();

        var output = (day < 10 ? '0' : '') + day + "-"
            + (month < 10 ? '0' : '') + month + '-'
            + this.FECHA_INICIO.getFullYear();

        return output;
    }

    getFechaFin() {
        var month = this.FECHA_FIN.getMonth() + 1;
        var day = this.FECHA_FIN.getDate();

        var output = (day < 10 ? '0' : '') + day + "-"
            + (month < 10 ? '0' : '') + month + '-'
            + this.FECHA_FIN.getFullYear();

        return output;
    }
}
